'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Modal } from '@/components/ui/Modal';

interface User {
  id: string;
  name: string;
  email: string;
  role: string;
  permissions: string[];
}

interface Module {
  id: string;
  label: string;
  description: string;
}

const AVAILABLE_MODULES: Module[] = [
  { id: 'dashboard', label: 'Dashboard', description: 'View dashboard and analytics' },
  { id: 'patients', label: 'Patients', description: 'Manage patient records' },
  { id: 'appointments', label: 'Appointments', description: 'Manage appointments' },
  { id: 'users', label: 'Users', description: 'Manage users (admin only)' },
  { id: 'clients', label: 'Clients', description: 'Manage clients (admin only)' },
];

export default function PermissionsPage() {
  const [users, setUsers] = useState<User[]>([]);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [search, setSearch] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [tempPermissions, setTempPermissions] = useState<string[]>([]);

  // Modal state
  const [modal, setModal] = useState<{
    isOpen: boolean;
    title: string;
    message: string;
    type: 'success' | 'error' | 'warning' | 'info' | 'confirm';
  }>({
    isOpen: false,
    title: '',
    message: '',
    type: 'info'
  });

  const showModal = (
    title: string,
    message: string,
    type: 'success' | 'error' | 'warning' | 'info' | 'confirm' = 'info'
  ) => {
    setModal({ isOpen: true, title, message, type });
  };

  const closeModal = () => {
    setModal({ ...modal, isOpen: false });
  };

  useEffect(() => {
    loadUsers();
  }, [search]);

  const loadUsers = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/users${search ? `?q=${search}` : ''}`,
        {
          headers: { 'Authorization': `Bearer ${token}` }
        }
      );
      const data = await response.json();
      setUsers(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error('Failed to load users:', error);
      setUsers([]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSelectUser = (user: User) => {
    setSelectedUser(user);
    setTempPermissions(user.permissions || []);
  };

  const togglePermission = (moduleId: string) => {
    setTempPermissions(prev => {
      if (prev.includes(moduleId)) {
        return prev.filter(p => p !== moduleId);
      } else {
        return [...prev, moduleId];
      }
    });
  };

  const handleSavePermissions = async () => {
    if (!selectedUser) return;
    
    setIsSaving(true);
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/users/${selectedUser.id}/permissions`,
        {
          method: 'PATCH',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ permissions: tempPermissions })
        }
      );

      if (response.ok) {
        const updatedUser = await response.json();
        setUsers(prev => prev.map(u => u.id === updatedUser.id ? updatedUser : u));
        setSelectedUser(updatedUser);
        showModal('Success', 'Permissions updated successfully!', 'success');
      } else {
        const error = await response.json();
        showModal('Error', `Failed to update permissions: ${error.error || 'Unknown error'}`, 'error');
      }
    } catch (error) {
      console.error('Failed to save permissions:', error);
      showModal('Error', 'Failed to save permissions. Please try again.', 'error');
    } finally {
      setIsSaving(false);
    }
  };

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-3xl font-bold text-gray-900">Permission Management</h2>
        <p className="text-gray-600 mt-1">Manage user access to different modules</p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Users List */}
        <Card>
          <CardHeader>
            <CardTitle>Users</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="mb-4">
              <Input
                type="text"
                placeholder="Search users..."
                value={search}
                onChange={(e) => setSearch(e.target.value)}
              />
            </div>

            {isLoading ? (
              <div className="text-center py-8 text-gray-500">Loading users...</div>
            ) : users.length === 0 ? (
              <div className="text-center py-8 text-gray-500">No users found</div>
            ) : (
              <div className="space-y-2 max-h-[600px] overflow-y-auto">
                {users.map(user => (
                  <div
                    key={user.id}
                    onClick={() => handleSelectUser(user)}
                    className={`p-4 border rounded-lg cursor-pointer transition-all ${
                      selectedUser?.id === user.id
                        ? 'bg-blue-50 border-blue-500'
                        : 'bg-white border-gray-200 hover:border-blue-300'
                    }`}
                  >
                    <div className="flex justify-between items-start">
                      <div>
                        <p className="font-semibold text-gray-900">{user.name}</p>
                        <p className="text-sm text-gray-600">{user.email}</p>
                        <span className="inline-block mt-1 px-2 py-1 text-xs rounded bg-gray-100 text-gray-700">
                          {user.role}
                        </span>
                      </div>
                      <div className="text-right">
                        <p className="text-xs text-gray-500">
                          {user.permissions?.length || 0} permissions
                        </p>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Permission Editor */}
        <Card>
          <CardHeader>
            <CardTitle>
              {selectedUser ? `Permissions for ${selectedUser.name}` : 'Select a user'}
            </CardTitle>
          </CardHeader>
          <CardContent>
            {!selectedUser ? (
              <div className="text-center py-16 text-gray-500">
                <svg className="w-16 h-16 mx-auto mb-4 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z" />
                </svg>
                <p>Select a user from the list to manage their permissions</p>
              </div>
            ) : (
              <div className="space-y-4">
                <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
                  <p className="text-sm text-blue-800">
                    <strong>Note:</strong> Control which modules this user can access. 
                    Unchecked modules will be hidden from their dashboard.
                  </p>
                </div>

                <div className="space-y-3">
                  {AVAILABLE_MODULES.map(module => {
                    const isChecked = tempPermissions.includes(module.id);
                    const isDisabled = selectedUser.role === 'admin' && ['users', 'clients'].includes(module.id);
                    
                    return (
                      <div
                        key={module.id}
                        className={`border rounded-lg p-4 transition-all ${
                          isChecked ? 'bg-green-50 border-green-300' : 'bg-white border-gray-200'
                        }`}
                      >
                        <label className="flex items-start cursor-pointer">
                          <input
                            type="checkbox"
                            checked={isChecked}
                            onChange={() => togglePermission(module.id)}
                            disabled={isDisabled}
                            className="mt-1 w-5 h-5 text-blue-600 rounded focus:ring-2 focus:ring-blue-500"
                          />
                          <div className="ml-3 flex-1">
                            <p className="font-semibold text-gray-900">{module.label}</p>
                            <p className="text-sm text-gray-600">{module.description}</p>
                            {isDisabled && (
                              <p className="text-xs text-gray-500 mt-1 italic">
                                Always enabled for admins
                              </p>
                            )}
                          </div>
                        </label>
                      </div>
                    );
                  })}
                </div>

                <div className="flex gap-3 pt-4">
                  <Button
                    onClick={handleSavePermissions}
                    disabled={isSaving}
                    className="flex-1 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700"
                  >
                    {isSaving ? (
                      <>
                        <svg className="animate-spin -ml-1 mr-2 h-5 w-5" fill="none" viewBox="0 0 24 24">
                          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                        </svg>
                        Saving...
                      </>
                    ) : (
                      'Save Permissions'
                    )}
                  </Button>
                  <Button
                    onClick={() => {
                      setSelectedUser(null);
                      setTempPermissions([]);
                    }}
                    className="bg-gray-500 hover:bg-gray-600"
                  >
                    Cancel
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Custom Modal */}
      <Modal
        isOpen={modal.isOpen}
        onClose={closeModal}
        title={modal.title}
        message={modal.message}
        type={modal.type}
      />
    </div>
  );
}
